.Technology {
	/* border: 1px solid red; */
	/* margin: auto; */
	width: 75%;
	/* padding: 0 5%; */
}

.PortfolioImage {
	/* border: 1px solid red; */
	z-index: 1;
	position: relative;
}

.PortfolioImage > img {
	border-radius: 50%;
	width: 100%;
}

.PortfolioImage .PortfolioInfo {
	/* border: 1px solid red; */
	background: linear-gradient(
		0deg,
		rgba(2, 0, 36, 0) 40%,
		rgba(9, 9, 121, 0) 40%,
		rgb(70, 90, 133) 85%
	);
	width: 100%;
	height: 100%;
	border-radius: 50%;
	z-index: 2;
	position: absolute;
}

.PortfolioInfo ul > li > div > svg {
	/* border: 1px solid red; */
	height: 45px;
	width: 45px;
}

.PortfolioInfo svg > circle {
	fill: none;
}

.PortfolioInfo svg > path {
	fill: white;
}

.PortfolioInfo > h3 {
	/* border: 1px solid red; */
	color: white;
	text-align: center;
	z-index: 3;
}

.PortfolioInfo > ul {
	/* border: 1px solid red; */
	justify-content: center;
	display: flex;
	position: relative;
	z-index: 3;
	padding: 0;
}

.PortfolioInfo > ul > li {
	/* border: 1px solid red; */
}

.Details {
	/* border: 1px solid red; */
	margin: auto;
	width: 85%;
	display: grid;
	padding: 3% 0 4% 0;
	grid-template-columns: 2fr 1fr;
	grid-template-rows: 0.3fr 0.7fr;
	grid-template-areas:
		"title links"
		"text text";
}

.Details > h2 {
	/* border: 1px solid red; */
	grid-area: title;
	/* margin: 0; */
	font-size: 30px;
	font-weight: 600;
	letter-spacing: 0.1em;
	margin: 2% 0 8% 0;
}

.PortfolioInfo > ul,
.Details > ul {
	/* border: 1px solid red; */
	align-items: center;
	grid-area: links;
	display: flex;
	list-style-type: none;
}

li {
	/* border: 1px solid red; */
	grid-area: link;
	padding: 0 5px;
}

.Details > p {
	/* border: 1px solid red; */
	grid-area: text;
	letter-spacing: 0;
	text-align: justify;
	line-height: 1.6;
	margin: 0;
}

.Details > ul {
	margin: 2% 0 15% 0;
}

.Details svg > circle {
	fill: #4b6b80;
}

.Details svg {
	-webkit-transition: -webkit-transform 0.8s ease-in-out;
	transition: transform 0.8s ease-in-out;
}

.Details svg:hover {
	/* fill: #5e88a4; */
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
}
